<template>
    <div class="box">
        <div class="box-header with-border">
            <h3 class="box-title">Sửa danh mục</h3>
        </div>
        <div class="box-body">
            <form @submit.prevent="handleSubmit">
                <div class="box-body">
                    <div class="row">
                        <div class="col-lg-6">
                            <div class="form-group">
                                <label>Danh mục cha</label>
                                <Select2 v-model="category.myValue" name="parent_id" v-validate="'required'"  :options="category.myOptions" @change="myChangeEvent($event)" />
                                <span class="error">{{ errors.first("parent_id") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Tên danh mục</label>
                                <input v-model="post.name" name="name" v-validate="'required|min:3|max:50'" type="text" :class="{'form-control':true, 'error-boder': errors.first('name')}" placeholder="Nhập tên danh mục...">
                                <span class="error">{{ errors.first("name") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Title</label>
                                <input v-model="post.title" name="title" v-validate="'required|min:3|max:50'" type="text" :class="{'form-control':true, 'error-boder': errors.first('title')}" placeholder="Nhập title...">
                                <span class="error">{{ errors.first("title") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Icon</label>
                                <input v-validate="'image'" data-vv-as="image" type="file" name="icon" v-on:change="onInputChange">
                                <span class="error">{{ errors.first("icon") }}</span>
                                <div id="upload-icon">
                                    <div class="upload-icon imgPreview" v-bind:style="{backgroundImage: 'url('+ imgPreview +')'}"></div>
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-6">
                            
                        </div>
                    </div>
                    <button type="submit" class="btn btn-primary" >Sửa</button>
                </div>
                <!-- /.box-body -->
            </form>
        </div>
        <!-- /.box-body -->
    </div>
</template>
<script>
import axios from "axios";
import Select2 from 'v-select2-component';
import slug from "slug";

import { Validator } from 'vee-validate';
const dictionary = {
    vi: {
        custom: {
            'name' : {
                required: 'Vui lòng nhập tên danh mục !',
                min: 'Tên danh mục tối thiểu 3 ký tự !',
                max: 'Tên danh mục không được vượt quá 50 ký tự !'
            },
            "parent_id": {
                required: 'Vui lòng chọn danh mục cha !',
            },
            "icon":{
                image: "Vui lòng chọn định dạng hình ảnh !"
            },
            "title":{
                required: 'Vui lòng nhập title !',
                min: 'Title tối thiểu 3 ký tự !',
                max: 'Title không được vượt quá 50 ký tự !'
            }
        }
    }
}
Validator.localize(dictionary);
const validator = new Validator();
validator.localize('vi'); // Bây giờ validator sẽ tạo ra thông báo lỗi bằng tiếng việt

export default {
    metaInfo: {
        title: "Thêm danh mục"
    },
    components:{
        Select2
    },
    data(){
        return {
            api: this.$store.state.domainApi,
            post:{
                name: "",
                url: "",
                icon:"",
                title:"",
                open:0,
                parent_id:0,
                other:"{}"
            },
            file:{},
            imgPreview:{},
            category: {
                myValue: 0,
                myOptions:[],
                errors:[]
            }
        }
    },
    methods: {
        handleSubmit() {
            let name = this.post.name;
            this.post.url = slug(name.toLowerCase());
            this.$validator.validate().then(valid => {
                if (valid) {
                    const formData = new FormData();
                    
                    formData.set("id", this.post.id);
                    formData.append("name", this.post.name);
                    formData.append("url", this.post.url);
                    formData.append("icon", this.post.icon);
                    formData.append("title", this.post.title);
                    formData.append("open", this.post.open);
                    formData.append("parent_id", this.post.parent_id);
                    formData.append("other", this.post.other);
                    formData.append("file", this.file);

                    axios.patch(`${this.api}/admin/category`, formData,{
                        headers: {
                            Authorization: `Bearer ${localStorage.token}`
                        }
                    })
                    .then(response => {
                        if(response.data.success===1){
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                            this.$router.push({name: "route.admin.category.index"});
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                    });
                    
                }
            });
        },
        onInputChange(e){
            this.file = e.target.files[0];
            //phan laod anh local
            const reader = new FileReader();
            reader.onload = (e) => this.imgPreview = e.target.result;
            reader.readAsDataURL(this.file);
        },
        myChangeEvent: function(val){
            this.post.parent_id = val;
        },
        formatJson(data, parent_id=0, str="--------|"){
            var parent = [];
            for(var item of data){
                if(item.parent_id == parent_id){
                    var obj = {
                        id: item.id,
                        text: str + item.name
                    };
                    parent.push(obj);
                    var result = this.formatJson(data.filter(e=>e.parent_id != parent_id), item.id, str+"--------|");
                    parent = parent.concat(result);
                }
            }
            return parent;
        }
    },
    created(){
        
        let id = this.$route.params.id;
        if(id){
            this.category.myValue = id;
            this.post.parent_id = id;
        }
        // get option
        axios.get(`${this.api}/admin/category`,{
            headers: {
                Authorization: `Bearer ${localStorage.token}`
            }
        })
        .then(response => {
            var obj = [
                {id: 0, text: "--ROOT--"}
            ];
            this.category.myOptions = obj.concat(this.formatJson(response.data));
        })
        .catch(e => {
            this.errors.push(e)
        });
        // get
        axios.get(`${this.api}/admin/category/${id}`,{
            headers:{
                Authorization: `Bearer ${localStorage.token}`
            }
        })
        .then(response => {
            this.post.id = id;
            this.post.name = response.data.name;
            this.post.url = response.data.url;
            this.post.title = response.data.title;
            this.post.icon = response.data.icon;
            this.imgPreview = `${this.api}/image/${response.data.icon}`;
            this.post.open = response.data.open;
            this.post.parent_id = response.data.parent_id;
        })
        .catch(e => {
            console.log(e);
        });
    }
}
</script>

<style>
.form-group{
    margin-bottom: 0px;
}
.form-group label{
    display: block;
}
.error{
    font-weight: 600;
    color: red;
    margin-left: 12px;
    margin-top: 3px;
    margin-bottom: 10px;
    display: inline-block;
}
.error-boder{
    border-color: red !important;
}
#upload-icon{
    background: #009688;
    display: block;
    max-width: 230px;
    height: 100px;
    outline: 2px dashed white;
    outline-offset: -10px;
    padding: 20px;
    color: white;
    position: relative;
}
#upload-icon .title{
    display: block;
    text-align: center;
    padding: 17px;
    font-weight: 600;
}
.imgPreview{
    height: 100%;
    width: 100%;
    background-size:contain;
    background-repeat: no-repeat;
    background-position: center;
}
</style>